<template>
	<div>
    <div v-on:click="toggleCategory()" class="menuCategory">
      <i v-if="toggledMenu" class="ion-arrow-down-b"></i><i v-if="!toggledMenu" class="ion-arrow-right-b"></i>&nbsp;&nbsp;{{ categoryName }}
    </div>
    <div class="menuList" v-if="toggledMenu" transition="expand">
      <div v-for="(item, idx) in items" :key="idx">
        {{ item.name }} {{ item.price }}
      </div>
    </div>
	</div>
</template>


<script>
export default {
  name: "MenuCategory",
  props: {
    categoryName: {
      type: String,
      required: true
    },
    items: {
      type: Array,
      required: true
    },
  },
  data () {
    return {
      toggledMenu: false
    }
  },
  methods: {
    toggleCategory () {
      this.toggledMenu = !this.toggledMenu
    }
  }
};
</script>

<style scoped>
  .menuCategory{
    padding: 7.8px 0;
  }
  .menuList{
    padding: 3.8px;
  }
</style>


